body {
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
  overflow-x: hidden;
}

:root {
  --sk-size: 40px;
  --sk-color: #333;
}

html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

*, :after, :before {
  box-sizing: border-box;
}

b, h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

.footer {
  width: 100%;
  color: #545454;
  text-align: center;
  background-color: #f7f7f7;
}

.footer_text {
  margin-bottom: 4px;
}

.footer_heart {
  fill: #b92f2c;
  vertical-align: auto;
  color: #b92f2c;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

body.is-open {
  transition: all 2.5s linear;
  overflow: hidden;
}

.js-lightbox.is-open {
  transition: all 2.5s linear;
  transform: scale(1);
}

.lightbox.is-open {
  opacity: 1;
  pointer-events: auto;
  transition: all 2.5s linear;
}

.lightbox {
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .56);
  transition: all 2.5s linear;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.lightbox_title {
  letter-spacing: normal;
  text-align: center;
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.14;
}

.lightbox_content {
  color: #000;
  max-width: 800px;
  background-color: rgba(255, 255, 255, .9);
  border-radius: 4px;
  padding: 20px;
  position: relative;
}

.lightbox_fourrowel {
  background-color: #fff;
  border-radius: 0 0 4px 4px;
  justify-content: space-around;
  align-items: center;
  margin-top: 10px;
  padding-left: 10px;
  display: flex;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.sociallinks {
  border-radius: 50%;
  padding: 10px;
}

.sociallinks_icon {
  fill: #000;
}

.lightbox .icon_link:hover .icon_link:focus {
  fill: #b92f2c;
  cursor: pointer;
}

.lightbox_button, .icon {
  width: 20px;
  height: 20px;
  fill: #000;
  cursor: pointer;
  background-color: #b92f2c;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 30%;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.icon {
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

.lightbox_button:hover, .lightbox_button:focus {
  fill: #000;
}

.icon:hover, .icon:focus {
  fill: #b92f2c;
}

.lightbox_img {
  border: 2px solid #b92f2c;
  border-radius: 5%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.comcontent {
  letter-spacing: normal;
  text-align: left;
  width: 70%;
  margin: 0;
  padding: 10px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16;
}

.comcontent_name {
  color: #000;
  margin: 0;
  padding-left: 10px;
}

.comcontent_subtitle {
  color: #b92f2c;
  padding-top: 4px;
  padding-left: 10px;
}

